<template>
    <div class="input-block" :class="className">
        <input
            :id="id"
            :type="type"
            class="input-block__input"
            placeholder=" "
            v-model="inputText"
            @keyup="handleInput"
            @click="handleClick"
            @blur="onBlur"
            required
        >
        <label :for="id" class="input-block__label">{{ label }}</label>
        <div
            class="input-block__email-error"
            v-if="showError"
        >
            {{ errorMessage }}
        </div>
        <slot></slot>
    </div>
</template>

<script>
export default {
    props: {
        className: String,
        id: {
            type: [String, Number],
            required: true
        },
        type: {
            type: String,
            default: 'text'
        },
        label: {
            type: String,
            required: true
        },
        showError: {
            type: Boolean,
            default: false
        },
        errorMessage: {
            type: String,
            required: true
        },
        value: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            inputText: this.value
        }
    },
    methods: {
        handleInput: function() {
            this.$emit('handle-input', this.inputText)
        },
        handleClick: function() {
            this.$emit('on-click')
        },
        onBlur: function() {
            this.$emit('on-blur')
        }
    }
}
</script>

