            color: var(--color-dusty-gray);
        }
    }

    &__label {
        position: absolute;
        z-index: 1;
        top: 15px;
        left: 0;
        font-size: var(--default-font-size);
        line-height: var(--default-line-height);
        transition: top var(--tr-xshort) ease, font-size var(--tr-xshort) ease;
    }

    &__show-password {
        fill: var(--color-white);
        position: absolute;
        z-index: 6;
