        cursor: pointer;
        
        &_active {
            fill: var(--color-cerulean);
        }
    }

    &__error {
        border-color: var(--color-your-pink);
        
        &:focus {
            border-color: var(--color-your-pink);
        }
    }

    &__verified {
        position: absolute;
        z-index: 6;
